.mv {
  background-image: url(../img/home/main.png);
  
  background-color: black;
  min-height: 100vh;
  background-repeat: no-repeat;
  position: relative;
  .overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    opacity: 0.78;
  }

  &-box {
    padding: 280px 160px 100px 160px;
    position: relative;
    z-index: 100;
    color: white;

    &-ttl {
      font-size: 6.6rem;
      font-family: 'sfProDisplay';
      font-weight: 600;
      margin: 0;
      line-height: 1.2;
    }

    &-txt {
      font-size: 1.8rem;
      font-weight: 600;
      margin: 4rem 0;
    }
  }
}

.mv.section01 {
  background-image: unset;
  min-height: 60vh;
  .mv-img {
    position: absolute;
    width: 45%;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    right: 50px;
  }
  .mv-box {
    padding-top: 180px;
  }
}

.mv.section02 {
  background-image: unset;
  min-height: 60vh;
  .mv-img {
    position: absolute;
    width: 45%;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    right: 50px;
  }
  .mv-box {
    padding-top: 180px;
  }
}

.mv.section03 {
  background-image: unset;
  min-height: 60vh;
  .mv-img {
    position: absolute;
    width: 45%;
    top: 50%;
    transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    right: 50px;
  }
  .mv-box {
    padding-top: 180px;
  }
}

@media (max-width: 1200px) {
  .mv {
    &-box {
      padding-left: 120px;
      padding-right: 120px;
      padding-top: 250px;

      &-ttl {
        font-size: 50px;
      }
  
      &-txt {
        font-size: 16px;
        margin: 30px 0;
      }
    }
  }
  .mv.section01 {
    .mv-img {
      width: 60%;
    }
  }
  .mv.section02 {
    .mv-img {
      width: 60%;
    }
  }
  .mv.section03 {
    .mv-img {
      width: 60%;
    }
  }
}

@media (max-width: 767px) {
  .mv {
    background-position: -100px;
    background-size: cover;

    &-box {
      padding-top: 150px;
      padding-left: 100px;
      padding-right: 100px;
      &-ttl {
        font-size: 36px;
      }
  
      &-txt {
        font-size: 16px;
        margin: 40px 0;
      }
    }
  }
  .mv.section01 {
    min-height: 80vh;
    .mv-img {
      width: 90%;
      right: 5%;
    }
    .mv-box {
